const RecentReportContainer = (
  props,
  { setUserData, setIsLoggedIn, setUserRole, setUserDepartment }
) => {
  const auth = useContext(AuthContext);
  const history = useHistory();
  const [recentReportDays, setRecentReportDays] = useState(30);
  const [fetchData, setFetchData] = useState();

  useEffect(() => {
    const token = getAuthToken();
    const config = {
      headers: {
        authorization: `Bearer ${token}`,
      },
    };
    const getRecentReportsPics = async () => {
      //Event.preventDefault();
      try {
        const res = await getRecentReportsPicsApi(
          // auth.Role,
          recentReportDays
        ).get("", config);

        if (res.data) {
          const arr = res.data.ReportList.map((x, idx) => {
            x.key = idx;
            return x;
          });
          setFetchData(arr);
        } else {
        }
      } catch (err) {
        console.log(err);
        if (err.response.status === 401) {
          removeAuthToken();
          message.info("");
          setUserData(null);
          setIsLoggedIn(false);
          setUserRole(null);
          setUserDepartment(null);
          history.push("/");
        }
      }
    };
    if (auth.Role !== null) getRecentReportsPics();
  }, [
    recentReportDays,
    auth.Role,
    setUserData,
    setIsLoggedIn,
    setUserRole,
    setUserDepartment,
    history,
  ]);

  return (
    <RecentReportWithSpinner
      isLoading={!!!fetchData}
      repoData={fetchData}
      setRecentReportDays={setRecentReportDays}
      setFetchData={setFetchData}
      recentReportDays={recentReportDays}
      setUserData={setUserData}
      setIsLoggedIn={setIsLoggedIn}
      setUserRole={setUserRole}
      setUserDepartment={setUserDepartment}
    />
  );
};

export default RecentReportContainer;
