import React, { useContext } from "react";
import { Menu } from "antd";
import {
  AppstoreOutlined,
  MailOutlined,
  SettingOutlined,
} from "@ant-design/icons";
import { AuthContext } from "../../shared/contexts/auth-context";
const { SubMenu } = Menu;

const GroupDistribute = ({
  dep,
  setChooseGroup,
  groupInfo,
  setChooseDepartment,
}) => {
  const auth = useContext(AuthContext);

  const handleClick = ({ item, key, keyPath, domEvent }) => {
    //console.log(keyPath);
    setChooseGroup(keyPath[0]);
    setChooseDepartment(keyPath[1]);
  };
  const Template = () => {
    if (auth.Department === "Admin") {
      groupInfo
        .filter(
          (y) => y.departmentname !== "Admin" && y.departmentname !== "Other"
        )
        .map((x, index) => {
          const id = String(index + 1);
          return {
            title: x.departmentname,
            key: id,
          };
        });
    } else if (auth.Department !== "Admin" && auth.Role === "Leader") {
      groupInfo
        .filter((y) => y.departmentname === auth.Department)
        .map((x, index) => {
          const id = String(index + 1);
          return {
            title: x.departmentname,
            key: id,
          };
        });
    }
  };

  return (
    <Menu
      onClick={handleClick}
      style={{ width: 256 }}
      defaultSelectedKeys={["1"]}
      //defaultOpenKeys={["sub1"]}
      mode="inline"
    >
      <>
        {auth.Department === "Admin" &&
          groupInfo
            .filter(
              (x) =>
                x.departmentname !== "Admin" && x.departmentname !== "Other"
            )
            .map((x) => (
              <SubMenu
                key={x.departmentname}
                icon={<AppstoreOutlined />}
                title={x.departmentname}
                // onTitleClick={({ key, domEvent }) => console.log(key, domEvent)}
              >
                {x.grouplist.map((y) => (
                  <Menu.Item key={y}>{y}</Menu.Item>
                ))}
              </SubMenu>
            ))}
        {auth.Department !== "Admin" &&
          auth.Role === "Leader" &&
          groupInfo
            .filter((y) => y.departmentname === auth.Department)
            .map((x) => (
              <SubMenu
                key={x.departmentname}
                icon={<AppstoreOutlined />}
                title={x.departmentname}
                // onTitleClick={({ key, domEvent }) => console.log(key, domEvent)}
              >
                {x.grouplist.map((y) => (
                  <Menu.Item key={y}>{y}</Menu.Item>
                ))}
              </SubMenu>
            ))}
      </>
    </Menu>
  );
};

export default GroupDistribute;
