import React, { useEffect, useState, useContext } from "react";
import {
  Radio,
  Form,
  Select,
  Modal,
  Input,
  message,
  Tooltip,
  Typography,
  Space,
} from "antd";
import { postAuthorizeApi, getUserInfoApi } from "../../api-list/api-list";
import { getAuthToken, removeAuthToken } from "../../shared/util/getAuth";
import "./FormData.css";
import { useHistory } from "react-router-dom";
import { AuthContext } from "../../shared/contexts/auth-context";
import { InfoCircleOutlined } from "@ant-design/icons";

const FormData = ({
  editData,
  setUseInfo,
  ModalOpen,
  setModalOpen,
  setUserData,
  setIsLoggedIn,
  setUserRole,
  groupInfo,
}) => {
  const [form] = Form.useForm();
  //form.setFieldsValue(editData);
  useEffect(() => {
    form.setFieldsValue(editData);
  }, [editData, form, ModalOpen]);
  //form.setFieldsValue(editData);
  const auth = useContext(AuthContext);
  //const [department, setDepartment] = useState("");
  const [disabledRole, setDisabledRole] = useState(false);
  const [disabledGroup, setDisabledGroup] = useState(false);
  const history = useHistory();
  const token = getAuthToken();
  const config = {
    headers: {
      authorization: `Bearer ${token}`,
    },
  };

  const handleOk = () => {
    form
      .validateFields()
      .then((values) => {
        //console.log(values);
        handleOnSendCheck(values);
        //   values.WeightDate = woDateTime;
        //   values.WeightTest = values.ProcTest;
        //   values.WeightResult = values.ProcResult;
        //   values.WeightSigner = auth.account;

        //Object
        // let aa = Object.keys(values)
        //   .filter((x) => x !== "ProcTest" && x !== "ProcResult")
        //   .reduce((obj, key) => {
        //     obj[key] = values[key];
        //     return obj;
        //   }, {});

        // form.resetFields();
        //setIsModalVisible(false);
        //handleOnExcute(aa);
      })
      .catch((info) => {
        console.log("Validate Failed:", info);
      });
  };

  const handleOnSendCheck = async (values) => {
    //const postJson = values
    //console.log(checked, postJson);
    //console.log(postJson);
    //if(values.group)
    try {
      await postAuthorizeApi
        .post("", values, config)
        .then((ress) => {
          //console.log(ress.data);
          getUserInfoList();
          setModalOpen(false);
          //fetchingDataAsync();

          /*setTimeout(() => {
            handleOnCancel();
          }, 3000)*/
        })
        .catch((err) => {
          //setStatusCheck(false)
          console.log(err);
          if (err.response.status === 401) {
            removeAuthToken();
            message.info("");
            setUserData(null);
            setIsLoggedIn(false);
            setUserRole(null);
            history.push("/");
          }
        });
    } catch (e) {
      console.log(e);
    }
  };

  const getUserInfoList = async () => {
    //Event.preventDefault();
    try {
      const res = await getUserInfoApi.get("", config);
      if (res.data) {
        // console.log(res.data);
        let userArr = res.data.users.map((x, idx) => {
          x.key = idx;
          return x;
        });
        setUseInfo(userArr);
        //return res.data.Path;
      }
    } catch (err) {
      console.log(err);
    }
  };

  const handleCancel = () => {
    //setEditData({ account: "", department: "", check: false });
    // form.setFieldsValue({
    //   account: "",
    //   department: "",
    //   group: "",
    //   role: "",
    //   check: false,
    // });
    //setEditData();
    setModalOpen(false);
    form.resetFields();
  };

  //const onCurrencyChange = (newCurrency) => {
  // if (!("currency" in value)) {
  //   setCurrency(newCurrency);
  // }
  // triggerChange({
  //   currency: newCurrency,
  // });
  //};
  //const
  const change = (changedValues, allValues) => {
    //console.log(changedValues, allValues);
    if (allValues.role === "Leader") setDisabledGroup(true);
    else if (allValues.role === "Member") setDisabledGroup(false);
    //if (changedValues[0].value === "Leader") setDisabledGroup(true);
    //else if (changedValues[0].value === "Member") setDisabledGroup(false);
  };

  useEffect(() => {
    if (editData.department === "Admin") {
      setDisabledRole(true);
      setDisabledGroup(true);
    } else {
      setDisabledRole(false);
      //setDisabledGroup(false);
      /*if (editData.role === "Leader") {
        setDisabledGroup(true);
      } else setDisabledGroup(false);*/
    }
  }, [editData.department, editData.role]);

  return (
    <Modal
      title="Edit"
      visible={ModalOpen}
      onOk={handleOk}
      okText="Save"
      onCancel={handleCancel}
    >
      <Form
        form={form}
        labelCol={{
          span: 4,
        }}
        wrapperCol={{
          span: 14,
        }}
        layout="horizontal"
        // initialValues={{
        //   account: editData.account,
        //   department: editData.department,
        //   check: editData.check,
        //   role: editData.role,
        //   group: editData.group,
        // }}
        onValuesChange={change}
        //onFieldsChange={change}
      >
        <Form.Item label="" name="account">
          <Input disabled /*defaultValue={editData.account}*/ size="middle" />
        </Form.Item>
        <Form.Item label="" name="department">
          <Select
            disabled={
              auth.Department !== "Admin"
              //(auth.Department === "Admin" && auth.Role === "Leader")
            }
            //mode="multiple"
            className="select-department"
            placeholder="department"
            style={{
              width: "50%",
              border: "1px solid #d9d9d9",
            }}
            //defaultChecked={editData.department}
            //defaultValue={editData.department}
            //value={value.currency || currency}
            //onChange={onCurrencyChange}
            //onChange={() => handleChange(text, record)}
            size="middle"
            optionLabelProp="label"
          >
            {groupInfo.map((x) => (
              <Select.Option value={x.departmentname}>
                {x.departmentname}
              </Select.Option>
            ))}
          </Select>
        </Form.Item>
        <Form.Item
          label=""
          name="role"
          rules={[{ required: true, message: "!" }]}
        >
          <Select
            disabled={disabledRole}
            //mode="multiple"
            className="select-role"
            placeholder="role"
            style={{
              width: "50%",
              border: "1px solid #d9d9d9",
            }}
            //defaultChecked={editData.department}
            //defaultValue={editData.department}
            //value={value.currency || currency}
            //onChange={onCurrencyChange}
            //onChange={() => handleChange(text, record)}
            size="middle"
            optionLabelProp="label"
          >
            <Select.Option value="Leader" label="Leader">
              Leader
            </Select.Option>
            <Select.Option value="Member" label="Member">
              Member
            </Select.Option>
          </Select>
        </Form.Item>
        {/* <Form.Item label="">
          <Space> */}
        <Form.Item
          label=""
          name="group"
          rules={[{ required: !disabledGroup, message: "!" }]}
        >
          <Select
            placeholder="Please select group"
            size="middle"
            optionLabelProp="label"
            disabled={disabledGroup}
            //defaultValue="lucy"
            style={{ width: 200, border: "0.1px solid lightgray" }}
            //onChange={handleGrp}
            //value={group}
            //bordered={true}
          >
            {groupInfo
              .filter(
                (x) =>
                  x.departmentname === editData.department &&
                  editData.department !== ""
              )
              .map((x) =>
                x.grouplist.map((y) => (
                  <Select.Option value={y} label={y}>
                    {y}
                  </Select.Option>
                ))
              )}
          </Select>
          {/* <Select
            //disabled
            //mode="multiple"
            className="select-department"
            placeholder="department"
            style={{
              width: "50%",
              border: "1px solid #d9d9d9",
            }}
            //defaultChecked={editData.department}
            //defaultValue={editData.department}
            //value={value.currency || currency}
            //onChange={onCurrencyChange}
            //onChange={() => handleChange(text, record)}
            size="middle"
            optionLabelProp="label"
          >
            <Select.Option value="CIM" label="CIM">
              CIM
            </Select.Option>
            <Select.Option value="QA" label="QA">
              QA
            </Select.Option>
            <Select.Option value="Admin" label="Admin">
              Admin
            </Select.Option>
          </Select> */}
        </Form.Item>
        {/* <Tooltip title="Member">
              <Typography.Link href="#API" style={{ paddingLeft: "10px" }}>
                <InfoCircleOutlined />
              </Typography.Link>
            </Tooltip>
          </Space>
        </Form.Item> */}
        <Form.Item label="" name="check">
          {/* <Checkbox
              style={{ textAlign: "center", paddingLeft: "30px" }}
              //defaultChecked={text}
              //name={record.account}
              //checked={checked[record.account]}
              onChange={handleOnClick}
            /> */}
          <Radio.Group
            //options={optionsWithDisabled}
            //onChange={onChange4}
            //value={value4}
            //defaultValue={editData.check}
            optionType="button"
            buttonStyle="solid"
            size="middle"
          >
            <Radio.Button value={true}></Radio.Button>
            <Radio.Button value={false} disabled={editData.check}>
              
            </Radio.Button>
          </Radio.Group>
        </Form.Item>
      </Form>
    </Modal>
  );
};

export default FormData;
