import React, { useContext } from "react";
import { Breadcrumb, Menu } from "antd";
import { NavLink } from "react-router-dom";
import { AuthContext } from "../../shared/contexts/auth-context";
const menu = (userRole) => (
  <Menu>
    <Menu.Item>
      <NavLink to="/scheduler">Scheduler</NavLink>
    </Menu.Item>
    <Menu.Item>
      <NavLink to="/report">Report</NavLink>
      {/* <a target="_blank" rel="noopener noreferrer" href="./report">
        Report
      </a> */}
    </Menu.Item>
    {userRole === "Admin" && (
      <>
        <Menu.Item>
          <NavLink to="/authority">Authority</NavLink>
        </Menu.Item>
      </>
    )}
  </Menu>
);

const Breadcrumbs = ({ title }) => {
  const auth = useContext(AuthContext);
  return (
    <Breadcrumb>
      <Breadcrumb.Item>
        <NavLink to="/">Home</NavLink>
      </Breadcrumb.Item>
      {title !== "Recent Reports" && (
        <>
          <Breadcrumb.Item overlay={menu(auth.Role)}>
            {" "}
            <NavLink to="/authority">Authority</NavLink>
          </Breadcrumb.Item>
          {title === "Group Setting" && (
            <Breadcrumb.Item>Group Setting</Breadcrumb.Item>
          )}
          {title === "Auth Setting" && (
            <Breadcrumb.Item>Auth Setting</Breadcrumb.Item>
          )}
        </>
      )}
    </Breadcrumb>
  );
};

export default Breadcrumbs;
