import React, { useState, useEffect, useContext } from "react";
import ControlBar from "../components/ControlBar";
import DataTable from "../components/DataTable";
import PageHeaders from "../components/PageHeaders";
import { getSchedulerListApi } from "../../api-list/api-list";
import { useGetData } from "../../shared/hooks/get-hooks";
import WithSpinner from "../../shared/components/UIELements/WithSpinner";
import { AuthContext } from "../../shared/contexts/auth-context";
import { getAuthToken, removeAuthToken } from "../../shared/util/getAuth";
import { message } from "antd";
import { useHistory } from "react-router";
const DataTableWithSpinner = WithSpinner(DataTable);
const Scheduler = ({ setUserData, setIsLoggedIn, setUserRole }) => {
  const [changeData, setChangeData] = useState([]);
  const [scheduleData, setScheduleData] = useState();
  const auth = useContext(AuthContext);
  const history = useHistory();
  /*let scheduleData = [];
  const [fetchData, fetchingDataAsync] = useGetData(
    getSchedulerListApi,
    config
  );
  if (fetchData) {
    scheduleData = fetchData.List.map((x, idx) => {
      x.key = idx;
      return x;
    });
  }*/

  useEffect(() => {
    const token = getAuthToken();
    const config = {
      headers: {
        authorization: `Bearer ${token}`,
      },
    };
    const getReportsList = async () => {
      //Event.preventDefault();
      try {
        const res = await getSchedulerListApi.get("", config);

        if (res.data) {
          let arr = res.data.List.map((x, idx) => {
            x.key = idx;
            return x;
          });

          setScheduleData(arr);
          //return res.data.Path;
        }
      } catch (err) {
        console.log(err);
        if (err.response.status === 401) {
          removeAuthToken();
          message.info("");
          setUserData(null);
          setIsLoggedIn(false);
          setUserRole(null);
          history.push("/");
        }
      }
    };
    getReportsList();
  }, [auth, setUserData, setIsLoggedIn, setUserRole, history]);
  //console.log(changeData);
  return (
    <div style={{ width: "90%" }}>
      <div style={{ display: "flex" }}>
        <PageHeaders title={"Scheduler"} back={false} />
        {/* <Divider style={{ marginBottom: "30px", width: "50%" }} /> */}
        <ControlBar
          changeData={changeData}
          //fetchingDataAsync={fetchingDataAsync}
          setScheduleData={setScheduleData}
          setUserData={setUserData}
          setIsLoggedIn={setIsLoggedIn}
          setUserRole={setUserRole}
        />
      </div>
      <DataTableWithSpinner
        isLoading={!!!scheduleData}
        setChangeData={setChangeData}
        scheduleData={scheduleData}
      />
    </div>
  );
};

export default Scheduler;
