import React, { useState, useEffect } from "react";
import ScriptTree from "../components/ScriptTree";
import ScriptSetting from "../components/ScriptSetting";
import PageHeadersSub from "../components/PageHeadersSub";
import {
  getSchedulerTreeDataApi,
  getSchedulerGrouplistApi,
} from "../../api-list/api-list";
import { useGetData } from "../../shared/hooks/get-hooks";
import {
  getAuthToken,
  setAuthToken,
  removeAuthToken,
} from "../../shared/util/getAuth";
import WithSpinner from "../../shared/components/UIELements/WithSpinner";
const ScriptSettingWithSpinner = WithSpinner(ScriptSetting);

const SchedulerEdit = (props) => {
  const [groupData, setGroupData] = useState();
  let treeData = [];
  // const [selectedScriptKey, setSelectedScriptKey] = useState(
  //   props.location.state.changeData[0].scriptkey
  // );

  const [selectedScript, setSelectedScript] = useState(
    props.location.state.changeData[0].script
  );
  const [selectedScriptPath, setSelectedScriptPath] = useState(
    props.location.state.changeData[0].scriptpath
  );
  const [fetchData] = useGetData(getSchedulerTreeDataApi);
  if (fetchData) {
    treeData = fetchData.children;
  }

  useEffect(
    () => {
      const getGroupInfoList = async () => {
        const token = getAuthToken();
        const config = {
          headers: {
            authorization: `Bearer ${token}`,
          },
        };
        try {
          const res = await getSchedulerGrouplistApi.get("", config);
          if (res.data) {
            setGroupData(res.data.departmentlist);
          }
        } catch (err) {
          console.log(err.response);
          //message.info("");
          //history.push("/auth");
          //auth.isLoggedIn
          //auth.logout();
          //RefreshToken("test", "test");
          /*setUserData(null);
            setIsLoggedIn(false);
            setUserRole(null);
            history.push("/");*/

          // if (err.response.status === 401) {
          //   try {
          //     await refreshTokenApi
          //       .post("", { refreshtoken: auth.UserData.RefreshToken }, config)
          //       .then((res) => {
          //         //console.log(res.data);
          //         setAuthToken(res.data.AccessToken);
          //       })
          //       .catch((errs) => {
          //         //console.log(errs.response);
          //         if (errs.response.status === 401) {
          //           removeAuthToken();
          //           message.info("");
          //           setUserData(null);
          //           setIsLoggedIn(false);
          //           setUserRole(null);
          //           history.push("/");
          //           //return Promise.reject(err);
          //         }
          //       });
          //     /*if (res) setAuthToken(res.data.AccessToken);
          //       else setAuthToken("");*/
          //   } catch (_err) {
          //     console.log(_err);
          //   }
          // }
        }
      };
      getGroupInfoList();
    },
    [
      // setUserData,
      // history,
      // setIsLoggedIn,
      // setUserRole,
      // auth.UserData.RefreshToken,
    ]
  );

  return (
    <div style={{ width: "90%" }}>
      <PageHeadersSub title={"Scheduler-Edit"} back={true} />
      {/* <Divider style={{ marginBottom: "30px", width: "50%" }} /> */}
      <div
        style={{ display: "flex", justifyContent: "center", height: "600px" }}
      >
        <ScriptTree
          treeData={treeData}
          setSelectedScript={setSelectedScript}
          setSelectedScriptPath={setSelectedScriptPath}
          selectedScript={selectedScript}
          //setSelectedScriptKey={setSelectedScriptKey}
          selectedScriptKey={props.location.state.changeData[0].scriptkey}
        />
        <ScriptSettingWithSpinner
          isLoading={!!!groupData}
          data={props.location.state.changeData}
          type="edit"
          selectedScript={selectedScript}
          selectedScriptPath={selectedScriptPath}
          setUserData={props.setUserData}
          setIsLoggedIn={props.setIsLoggedIn}
          setUserRole={props.setUserRole}
          groupData={groupData}
          //fetchingDataAsync={props.location.state.fetchingDataAsync}
        />
      </div>
    </div>
  );
};

export default SchedulerEdit;
