import React from "react";
import { Button, notification, Popconfirm, message } from "antd";
import { NavLink, useHistory } from "react-router-dom";
import {
  postSchedulerDeletehApi,
  getSchedulerListApi,
} from "../../api-list/api-list";
import { PlusOutlined, EditFilled, DeleteFilled } from "@ant-design/icons";
import { getAuthToken, removeAuthToken } from "../../shared/util/getAuth";

const ControlBar = ({
  changeData,
  fetchingDataAsync,
  setScheduleData,
  setUserData,
  setIsLoggedIn,
  setUserRole,
}) => {
  const openNotificationWithIcon = (type) => {
    let name;
    if (changeData.length !== 0) name = changeData[0].name;
    notification[type]({
      message: "",
      description: `${name}`,
      placement: "bottomLeft",
    });
  };
  const history = useHistory();
  const token = getAuthToken();
  const config = {
    headers: {
      authorization: `Bearer ${token}`,
    },
  };

  const handleOnDelete = async () => {
    const postJson = {
      name: changeData[0].name,
    };
    //console.log(postJson);
    //console.log(postJson);
    try {
      await postSchedulerDeletehApi
        .post("", postJson, config)
        .then((ress) => {
          getReportsList();
          //fetchingDataAsync();
          openNotificationWithIcon("success");
        })
        .catch((err) => {
          //setStatusCheck(false)
          console.log(err);
          if (err.response.status === 401) {
            removeAuthToken();
            message.info("");
            setUserData(null);
            setIsLoggedIn(false);
            setUserRole(null);
            history.push("/");
          }
        });
    } catch (e) {
      console.log(e);
    }
  };

  const getReportsList = async () => {
    try {
      const res = await getSchedulerListApi.get("", config);

      if (res.data) {
        let arr = res.data.List.map((x, idx) => {
          x.key = idx;
          return x;
        });

        setScheduleData(arr);
        //return res.data.Path;
      }
    } catch (err) {
      console.log(err);
      if (err.response.status === 401) {
        removeAuthToken();
        message.info("");
        setUserData(null);
        setIsLoggedIn(false);
        setUserRole(null);
        history.push("/");
      }
    }
  };

  return (
    <div
      style={{
        float: "right",
        //marginBottom: "30px",
        margin: "auto auto 3em auto",
        justifyContent: "space-between",
        marginRight: "5%",
      }}
    >
      <NavLink
        to={{
          pathname: "/scheduler/add",
          //state: { fetchingDataAsync: fetchingDataAsync },
        }}
      >
        <Button
          size="large"
          key="3"
          style={{
            backgroundColor: "#1CC153",
            borderRadius: "5px",
            color: "white",
            marginRight: "10px",
          }}
          icon={<PlusOutlined />}
        >
          ADD
        </Button>
      </NavLink>
      <NavLink
        to={{
          pathname: "/scheduler/edit",
          state: {
            changeData: changeData,
            //fetchingDataAsync: fetchingDataAsync,
          },
        }}
      >
        <Button
          size="large"
          key="2"
          type="primary"
          style={{ borderRadius: "5px", marginRight: "10px" }}
          disabled={changeData.length === 0}
          icon={<EditFilled />}
        >
          EDIT
        </Button>
      </NavLink>
      <Popconfirm
        placement="topLeft"
        title="?"
        //onConfirm={message.info("Clicked on Yes.")}
        onConfirm={(e) => handleOnDelete(e)}
        okText=""
        cancelText=""
      >
        <Button
          style={{ borderRadius: "5px", marginRight: "10px" }}
          size="large"
          key="1"
          type="danger"
          disabled={changeData.length === 0}
          icon={<DeleteFilled />}
        >
          DELETE
        </Button>
      </Popconfirm>
      {/* <Button
        size="large"
        key="1"
        type="danger"
        style={{ borderRadius: "5px", marginRight: "10px" }}
        disabled={changeData.length === 0}
        //onClick={() => openNotificationWithIcon("success")}
        onClick={() => handleOnDelete()}
      >
        DELETE
      </Button> */}
    </div>
  );
};

export default ControlBar;
