import React, { useState, useEffect, useContext } from "react";
import AuthControlBar from "../components/AuthControlBar";
import GroupDistribute from "../components/GroupDistribute";
import GroupTransfer from "../components/GroupTransfer";
import PageHeaders from "../components/PageHeadersSub";
import { AuthContext } from "../../shared/contexts/auth-context";
import { useHistory } from "react-router";
import { getAuthToken, removeAuthToken } from "../../shared/util/getAuth";
import { Button, message } from "antd";
import {
  getGroupInfoApi,
  refreshTokenApi,
  getUserInfoApi,
} from "../../api-list/api-list";
import WithSpinner from "../../shared/components/UIELements/WithSpinner";
const GroupDistributeWithSpinner = WithSpinner(GroupDistribute);
const GroupTransferWithSpinner = WithSpinner(GroupTransfer);

const AuthSetting = (
  props
  //{ setUserData, setIsLoggedIn, setUserRole, setUserDepartment }
) => {
  //console.log(setUserData);
  const [groupInfo, setGroupInfo] = useState();
  const auth = useContext(AuthContext);
  const history = useHistory();
  const [pane, setPane] = useState();
  const [useInfo, setUseInfo] = useState();
  const [chooseGroup, setChooseGroup] = useState("");
  const [chooseDepartment, setChooseDepartment] = useState("");
  useEffect(() => {
    // let mounted = true;
    //if (mounted) {
    const token = getAuthToken();
    const config = {
      headers: {
        authorization: `Bearer ${token}`,
      },
    };
    const getUserInfoList = async () => {
      try {
        const res = await getUserInfoApi.get("", config);
        if (res.data) {
          // console.log(res.data);
          let userArr = res.data.users.map((x, idx) => {
            x.key = idx;
            return x;
          });
          setUseInfo(userArr);
          //return res.data.Path;
        }
      } catch (err) {
        //console.log(err.response);
        if (err.response.status === 401) {
          removeAuthToken();
          message.info("");
          props.setUserData(null);
          props.setIsLoggedIn(false);
          props.setUserRole(null);
          props.setUserDepartment(null);
          history.push("/");
        }
        // if (err.response.status === 401) {
        //   try {
        //     await refreshTokenApi
        //       .post("", { refreshtoken: auth.UserData.RefreshToken }, config)
        //       .then((res) => {
        //         //console.log(res.data);
        //         setAuthToken(res.data.AccessToken);
        //       })
        //       .catch((errs) => {
        //         //console.log(errs.response);
        //         if (errs.response.status === 401) {
        //           removeAuthToken();
        //           message.info("");
        //           setUserData(null);
        //           setIsLoggedIn(false);
        //           setUserRole(null);
        //           setUserDepartment(null);
        //           history.push("/");
        //           //return Promise.reject(err);
        //         }
        //       });
        //     /*if (res) setAuthToken(res.data.AccessToken);
        // else setAuthToken("");*/
        //   } catch (_err) {
        //     console.log(_err);
        //     removeAuthToken();
        //     message.info("");
        //     setUserData(null);
        //     setIsLoggedIn(false);
        //     setUserRole(null);
        //     setUserDepartment(null);
        //     history.push("/");
        //   }
        // }
      }
    };
    getUserInfoList();
    // }

    // return function cleanup() {
    //   mounted = false;
    // };
  }, [
    props,
    //props.setUserData,
    history,
    //props.setIsLoggedIn,
    //props.setUserRole,
    //props.setUserDepartment,
    //auth.UserData.RefreshToken,
  ]);

  useEffect(() => {
    const getGroupInfoList = async () => {
      try {
        const res = await getGroupInfoApi.get("");
        if (res.data) {
          let panes = [];
          if (res.data.departmentlist.length !== 0) {
            panes = res.data.departmentlist /*new Array(dep).fill(null)*/
              .filter(
                (y) =>
                  y.departmentname !== "Admin" && y.departmentname !== "Other"
              )
              .map((x, index) => {
                const id = String(index + 1);
                return {
                  title: x.departmentname, //`Tab ${id}`,
                  //content: `Content of Tab Pane ${id}`,
                  key: id,
                };
              });
          }

          setPane(panes);
          setGroupInfo(res.data.departmentlist);
          //return res.data.Path;
        }
      } catch (err) {
        console.log(err.response);
      }
    };
    getGroupInfoList();
  }, []);

  return (
    <div style={{ width: "90%" }}>
      <div style={{ display: "flex" }}>
        <PageHeaders title={"Auth Setting"} back={true} />
        <AuthControlBar
          //isLoading={!!!useInfo}
          scheduleData={props.location.state.dep}
        />
      </div>
      <div
        style={{
          display: "flex",
          margin: "auto",
          borderStyle: "solid",
          padding: "20px",
          borderColor: "lightgray",
          height: "65vh",
          borderRadius: "3px",
          borderWidth: "0.5px",
          boxShadow: "0 3px 7px rgba(0, 0, 0, 0.3)",
          overflowY: "auto",
          width: "90%",
        }}
      >
        <GroupDistributeWithSpinner
          isLoading={!!!groupInfo}
          dep={props.location.state.dep}
          setChooseGroup={setChooseGroup}
          groupInfo={groupInfo}
          setChooseDepartment={setChooseDepartment}
        />
        <GroupTransferWithSpinner
          isLoading={!!!useInfo}
          json={useInfo}
          target={props.location.state.target}
          chooseGroup={chooseGroup}
          setUseInfo={setUseInfo}
          chooseDepartment={chooseDepartment}
          setUserData={props.setUserData}
          setIsLoggedIn={props.setIsLoggedIn}
          setUserRole={props.setUserRole}
          setUserDepartment={props.setUserDepartment}
        />
      </div>
    </div>
  );
};

export default AuthSetting;
