import React, { useContext } from "react";
import { Radio, Button, message } from "antd";
import { ReloadOutlined } from "@ant-design/icons";
import { getRecentReportsPicsApi } from "../../api-list/api-list";
import { AuthContext } from "../../shared/contexts/auth-context";
import { getAuthToken, removeAuthToken } from "../../shared/util/getAuth";
import { useHistory } from "react-router-dom";

const RecentReportControlBar = ({
  setRecentReportDays,
  setFetchData,
  recentReportDays,
  setUserData,
  setIsLoggedIn,
  setUserRole,
  setUserDepartment,
}) => {
  const auth = useContext(AuthContext);
  const history = useHistory();
  const handleOnChange = (e) => {
    setRecentReportDays(e.target.value);
    //setStartTime(dateStr[0]);
    //setEndTime(dateStr[1]);
  };

  const getRecentReportsPics = async () => {
    const token = getAuthToken();
    const config = {
      headers: {
        authorization: `Bearer ${token}`,
      },
    };
    //Event.preventDefault();
    try {
      const res = await getRecentReportsPicsApi(
        //auth.Role,
        recentReportDays
      ).get("", config);

      if (res.data) {
        const arr = res.data.ReportList.map((x, idx) => {
          x.key = idx;
          return x;
        });
        setFetchData(arr);
      } else {
      }
    } catch (err) {
      console.log(err);
      if (err.response.status === 401) {
        removeAuthToken();
        message.info("");
        setUserData(null);
        setIsLoggedIn(false);
        setUserRole(null);
        history.push("/");
      }
    }
  };

  return (
    <div
      style={{
        float: "right",
        margin: "auto auto 3em auto",
        //marginBottom: "30px",
        justifyContent: "space-between",
        marginRight: "5%",
      }}
    >
      <Radio.Group
        defaultValue="30"
        buttonStyle="solid"
        onChange={handleOnChange}
      >
        <Radio.Button value="7">7 days</Radio.Button>
        <Radio.Button value="14">14 days</Radio.Button>
        <Radio.Button value="30">1 month</Radio.Button>
        <Radio.Button value="90">3 month</Radio.Button>
      </Radio.Group>

      <Button
        type="default"
        style={{ marginLeft: "20px" }}
        //shape="circle"
        icon={<ReloadOutlined />}
        //size="small"
        onClick={() => getRecentReportsPics()}
      />

      {/* <RangePicker
        defaultValue={[
          moment(startTime, dateFormat),
          moment(endTime, dateFormat),
        ]}
        size="large"
        format={dateFormat}
        onChange={handleOnChange}
      /> */}
    </div>
  );
};

export default RecentReportControlBar;
