import React, { useEffect, useState, useContext } from "react";
import {
  getAuthToken,
  setAuthToken,
  removeAuthToken,
} from "../../shared/util/getAuth";
import { getGroupInfoApi, refreshTokenApi } from "../../api-list/api-list";
import { AuthContext } from "../../shared/contexts/auth-context";
import { message } from "antd";
import { useHistory } from "react-router-dom";
import GroupClassify from "../components/GroupClassify";
import PageHeaders from "../components/PageHeadersSub";
import WithSpinner from "../../shared/components/UIELements/WithSpinner";
import AuthControlBar from "../components/AuthControlBar";
const GroupClassifyWithSpinner = WithSpinner(GroupClassify);
//const AuthControlBarWithSpinner = WithSpinner(AuthControlBar);

const test = {
  departmentlist: [
    {
      grouplist: [],
      departmentname: "Other",
    },
    {
      grouplist: [],
      departmentname: "Admin",
    },
    {
      grouplist: ["TESTGROUP2", "TESTGROUP1"],
      departmentname: "CIM",
    },
  ],
};

const GroupSetting = (
  props
  //{ setUserData, setIsLoggedIn, setUserRole, setUserDepartment }
) => {
  //console.log(props.location.state);
  const [groupInfo, setGroupInfo] = useState();
  const auth = useContext(AuthContext);
  //console.log(auth.Department, auth.Role);
  const history = useHistory();
  const [pane, setPane] = useState();

  useEffect(() => {
    const token = getAuthToken();
    const config = {
      headers: {
        authorization: `Bearer ${token}`,
      },
    };
    const getGroupInfoList = async () => {
      //Event.preventDefault();
      try {
        const res = await getGroupInfoApi.get("", config);
        if (res.data) {
          //console.log(res.data.departmentlist);
          //   let userArr = res.data.departmentlist.map((x, idx) => {
          //     x.key = idx;
          //     return x;
          //   });
          let panes = [];
          if (res.data.departmentlist.length !== 0) {
            if (auth.Department === "Admin") {
              panes = res.data.departmentlist
                .filter(
                  (y) =>
                    y.departmentname !== "Admin" && y.departmentname !== "Other"
                )
                .map((x, index) => {
                  const id = String(index + 1);
                  return {
                    title: x.departmentname,
                    key: id,
                  };
                });
            } else if (auth.Department !== "Admin" && auth.Role === "Leader") {
              panes = res.data.departmentlist
                .filter((y) => y.departmentname === auth.Department)
                .map((x, index) => {
                  const id = String(index + 1);
                  return {
                    title: x.departmentname,
                    key: id,
                  };
                });
            }
          }
          setPane(panes);

          setGroupInfo(res.data.departmentlist);
          //return res.data.Path;/*new Array(dep).fill(null)*/
        }
      } catch (err) {
        console.log(err.response);
        if (err.response.status === 401) {
          removeAuthToken();
          message.info("");
          props.setUserData(null);
          props.setIsLoggedIn(false);
          props.setUserDepartment(null);
          props.setUserRole(null);
          history.push("/");
        }

        // if (err.response.status === 401) {
        //   try {
        //     await refreshTokenApi
        //       .post("", { refreshtoken: auth.UserData.RefreshToken }, config)
        //       .then((res) => {
        //         //console.log(res.data);
        //         setAuthToken(res.data.AccessToken);
        //       })
        //       .catch((errs) => {
        //         //console.log(errs.response);
        //         if (errs.response.status === 401) {
        //           removeAuthToken();
        //           message.info("");
        //           setUserData(null);
        //           setIsLoggedIn(false);
        //           setUserDepartment(null);
        //           setUserRole(null);
        //           history.push("/");
        //           //return Promise.reject(err);
        //         }
        //       });
        //     /*if (res) setAuthToken(res.data.AccessToken);
        //       else setAuthToken("");*/
        //   } catch (_err) {
        //     console.log(_err);
        //   }
        // }
      }
    };
    getGroupInfoList();
  }, [
    props,
    //setUserData,
    history,
    auth.Department,
    auth.Role,
    //setIsLoggedIn,
    //setUserRole,
    //setUserDepartment,
    //auth.UserData.RefreshToken,
  ]);

  return (
    <div style={{ width: "90%" }}>
      <div style={{ display: "flex" }}>
        <PageHeaders title={"Group Setting"} back={true} />
        <AuthControlBar
          //isLoading={!!!useInfo}
          scheduleData={props.location.state}
        />
      </div>
      <GroupClassifyWithSpinner
        isLoading={!!!groupInfo}
        dep={props.location.state}
        groupInfo={groupInfo}
        setGroupInfo={setGroupInfo}
        pane={pane}
        setPane={setPane}
        setUserData={props.setUserData}
        setIsLoggedIn={props.setIsLoggedIn}
        setUserRole={props.setUserRole}
        setUserDepartment={props.setUserDepartment}
        department={auth.Department}
      />
    </div>
  );
};

export default GroupSetting;
