import React, { useState, useRef } from "react";
import { Table, Button, Space, Input } from "antd";
import Highlighter from "react-highlight-words";
import { SearchOutlined } from "@ant-design/icons";

function onlyUnique(value, index, self) {
  return self.indexOf(value) === index;
}

const DataTable = ({ setChangeData, scheduleData }) => {
  let uniqueDep = scheduleData
    .map((x) => x.department)
    .filter(onlyUnique)
    .map((x) => ({
      text: x,
      value: x,
    }));
  let uniqueGroup = scheduleData
    .map((x) => x.group)
    .filter(onlyUnique)
    .map((x) => ({
      text: x,
      value: x,
    }));
  const [selectionType, setSelectionType] = useState("radio");
  const [nongId, setNongId] = useState([]);
  const total = scheduleData.length;

  const rowSelection = {
    selectedRowKeys: nongId,
    selectedRows: [],
    onChange: (selectedRowKeys, selectedRows) => {
      //console.log(selectedRowKeys);
      setChangeData(selectedRows);
      setNongId(selectedRowKeys);
      //   console.log(
      //     record,
      //     `selectedRowKeys: ${selectedRowKeys}`,
      //     "selectedRows: ",
      //     selectedRows
      //   );
    },
    // onSelect: (record, selected, selectedRows, nativeEvent) => {
    //   console.log(record, selected, selectedRows, nativeEvent);
    // },
    // getCheckboxProps: (record) => ({
    //   disabled: record.name === "Disabled User",
    //   // Column configuration not to be checked
    //   name: record.name,
    // }),
  };
  const [searchTexts, setSearchText] = useState("");
  const [searchedColumns, setSearchedColumn] = useState("");
  const searchInput = useRef(null);

  const handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
    setSearchText(selectedKeys[0]);
    setSearchedColumn(dataIndex);
  };

  const handleReset = (clearFilters) => {
    clearFilters();
    setSearchText("");
  };

  const getColumnSearchProps = (dataIndex) => ({
    filterDropdown: ({
      setSelectedKeys,
      selectedKeys,
      confirm,
      clearFilters,
    }) => (
      <div style={{ padding: 8 }}>
        <Input
          ref={searchInput}
          placeholder={`Search ${dataIndex}`}
          value={selectedKeys[0]}
          onChange={(e) =>
            setSelectedKeys(e.target.value ? [e.target.value] : [])
          }
          onPressEnter={() => handleSearch(selectedKeys, confirm, dataIndex)}
          style={{ marginBottom: 8, display: "block" }}
        />
        <Space>
          <Button
            type="primary"
            onClick={() => handleSearch(selectedKeys, confirm, dataIndex)}
            icon={<SearchOutlined />}
            size="small"
            style={{ width: 90 }}
          >
            Search
          </Button>
          <Button
            onClick={() => handleReset(clearFilters)}
            size="small"
            style={{ width: 90 }}
          >
            Reset
          </Button>
          <Button
            type="link"
            size="small"
            onClick={() => {
              confirm({ closeDropdown: false });
              setSearchText(selectedKeys[0]);
              setSearchedColumn(dataIndex);
              //   this.setState({
              //     searchText: selectedKeys[0],
              //     searchedColumn: dataIndex,
              //   });
            }}
          >
            Filter
          </Button>
        </Space>
      </div>
    ),
    filterIcon: (filtered) => (
      <SearchOutlined style={{ color: filtered ? "#1890ff" : undefined }} />
    ),
    onFilter: (value, record) =>
      record[dataIndex]
        ? record[dataIndex]
            .toString()
            .toLowerCase()
            .includes(value.toLowerCase())
        : "",
    onFilterDropdownVisibleChange: (visible) => {
      if (visible) {
        setTimeout(() => searchInput.current.select(), 100);
      }
    },
    render: (text) =>
      searchedColumns === dataIndex ? (
        <Highlighter
          highlightStyle={{ backgroundColor: "#ffc069", padding: 0 }}
          searchWords={[searchTexts]}
          autoEscape
          textToHighlight={text ? text.toString() : ""}
        />
      ) : (
        text
      ),
  });

  const columns = [
    {
      title: "Description Name",
      dataIndex: "name",
      ...getColumnSearchProps("name"),
    },
    {
      title: "Department",
      dataIndex: "department",
      filters: uniqueDep,
      onFilter: (value, record) => record.department.indexOf(value) === 0,
    },
    {
      title: "Group",
      dataIndex: "group",
      filters: uniqueGroup,
      onFilter: (value, record) => record.group.indexOf(value) === 0,
    },
    {
      title: "Script",
      dataIndex: "script",
    },
    {
      title: "Frequency",
      dataIndex: "frequency",
      filters: [
        {
          text: "By Day",
          value: "By day",
        },
        {
          text: "By Week",
          value: "By week",
        },
        {
          text: "By Month",
          value: "By month",
        },
      ],
      onFilter: (value, record) => record.frequency.indexOf(value) === 0,
    },
    {
      title: "Time Settings",
      dataIndex: "timesetting",
      render: (text, record) => {
        if (record.frequency === "By month") {
          return "" + record.dayindex + " - " + record.timesetting;
        } else if (record.frequency === "By week") {
          return "" + record.dayindex + " - " + record.timesetting;
        } else return "" + record.timesetting;
      },
    },
    {
      title: "Detail",
      dataIndex: "scriptpath",
      render: (text, record) => {
        if (text === "Script is not exist.") {
          return text;
        }
      },
      //...getColumnSearchProps("name"),
    },
  ];

  return (
    <Table
      scroll={{ x: "100%" }}
      style={{
        width: "90%",
        //height: "95%",
        margin: "auto",
        textAlign: "center",
        borderStyle: "solid",
        borderColor: "lightgrey",
        borderWidth: "1px",
        marginBottom: "50px",
        boxShadow: "0 2px 8px rgba(0, 0, 0, 0.26)",
      }}
      rowSelection={{
        type: selectionType,
        ...rowSelection,
      }}
      pagination={{
        position: ["bottomCenter"],
        pageSize: 8,
        total: total,
        showTotal: (total) => `Total ${total} scripts`,
      }}
      columns={columns}
      dataSource={scheduleData}
      rowKey={(record) => record.key}
      onRow={(record) => ({
        onClick: () => {
          //console.log(record.key);
          setNongId([record.key]);
          setChangeData([record]);
        },
      })}
    />
  );
};

export default DataTable;
