import React, { useState } from "react";
//import Card from "../../shared/components/UIELements/Card";
import {
  Input,
  Select,
  Radio,
  Button,
  Typography,
  Card,
  TimePicker,
  message,
} from "antd";
import moment from "moment";
import {
  postSchedulerAddApi,
  postSchedulerUpdateApi,
  getSchedulerGrouplistApi,
} from "../../api-list/api-list";
import { Link, useHistory } from "react-router-dom";
import styled from "styled-components";
import { getAuthToken, removeAuthToken } from "../../shared/util/getAuth";
const TimePickerStyle = styled.div`
  display: flex;
  margin-bottom: 3%;
`;

const FontStyle = styled.div`
  font-size: large;
  margin: auto 5px auto 0px;
`;

const format = "HH:mm";
const { Title } = Typography;
const { Option } = Select;

const ScriptSetting = ({
  data,
  type,
  selectedScript,
  selectedScriptPath,
  setUserData,
  setIsLoggedIn,
  setUserRole,
  groupData,
  //fetchingDataAsync,
}) => {
  const [descriptName, setDescriptName] = useState(
    data.length !== 0 ? data[0].name : ""
  );

  const [dayIndex, setDayIndex] = useState(
    data.length !== 0 ? data[0].dayindex : []
  );
  //const [hour, setHour] = useState();
  const [value, setValue] = useState(
    data.length !== 0 ? data[0].frequency : "By day"
  );
  const [time, setTime] = useState(
    data.length !== 0 ? data[0].timesetting : "00:00"
  );

  const [department, setDepartment] = useState(
    data.length !== 0 ? data[0].department : ""
  );
  const [group, setGroup] = useState(data.length !== 0 ? data[0].group : "");

  const handleOnTime = (time, str) => {
    //console.log(time, str);
    if (str !== "") setTime(str);
    else setTime("00:00");
  };

  function handleChange(value) {
    //console.log(`selected ${value}`);
    setDayIndex(value);
  }

  const onChange = (e) => {
    //console.log("radio checked", e.target.value);
    setValue(e.target.value);
  };

  // const onHourChange = (e) => {
  //   //console.log("hour:", e.target.value);
  //   setHour(e.target.value);
  //   //setValue(e.target.value);
  // };
  const history = useHistory();
  const handleOnSave = async (e) => {
    const token = getAuthToken();
    const config = {
      headers: {
        authorization: `Bearer ${token}`,
      },
    };

    if (selectedScript === "") {
      message.warning("Script");
    } else if (descriptName === "") {
      message.warning("Description Name");
    } else if (department === "") {
      message.warning("Department");
    } else if (group === "") {
      message.warning(`Group(Group)`);
    } else {
      if (type === "add") {
        const postJson = {
          name: descriptName,
          script: selectedScript,
          frequency: value,
          scriptpath: selectedScriptPath,
          dayindex: value === "By day" ? [] : dayIndex,
          timesetting: time,
          department: department,
          group: group,
        };
        try {
          await postSchedulerAddApi
            .post("", postJson, config)
            .then((ress) => {
              //console.log(ress.data);
              if (type === "edit") message.success("");
              else message.success("");

              history.push("/scheduler");
              // fetchingDataAsync();
              //if (ress.data !== "") setRows(ress.data);
              /*setTimeout(() => {
                handleOnCancel();
              }, 3000)*/
            })
            .catch((err) => {
              //setStatusCheck(false)
              message.error("!" + err);
              console.log(err);
              if (err.response.status === 401) {
                removeAuthToken();
                message.info("");
                setUserData(null);
                setIsLoggedIn(false);
                setUserRole(null);
                history.push("/");
              }
            });
        } catch (e) {
          message.error("(" + e + ")");
          console.log(e);
        }
      } else {
        const postJson = {
          id: data[0].id,
          name: data.length !== 0 ? data[0].name : "",
          editname: descriptName,
          script: selectedScript,
          frequency: value,
          scriptpath: selectedScriptPath,
          dayindex: value === "By day" ? [] : dayIndex,
          timesetting: time,
          department: department,
          group: group,
        };
        try {
          await postSchedulerUpdateApi
            .post("", postJson, config)
            .then((ress) => {
              //console.log(ress.data);
              if (type === "edit") message.success("");
              else message.success("");

              history.push("/scheduler");
              // fetchingDataAsync();
              //if (ress.data !== "") setRows(ress.data);
              /*setTimeout(() => {
                handleOnCancel();
              }, 3000)*/
            })
            .catch((err) => {
              //setStatusCheck(false)
              message.error("!" + err);
              console.log(err);
              if (err.response.status === 401) {
                removeAuthToken();
                message.info("");
                setUserData(null);
                setIsLoggedIn(false);
                setUserRole(null);
                history.push("/");
              }
            });
        } catch (e) {
          message.error("(" + e + ")");
          console.log(e);
        }
      }
    }
    //console.log(postJson);
  };

  const children = [];
  for (let i = 1; i < 32; i++) {
    children.push(<Option key={i.toString()}>{i.toString()}</Option>);
  }

  const handleDep = (value) => {
    setDepartment(value);
  };
  const handleGrp = (value) => {
    setGroup(value);
  };
  return (
    <Card
      //title="Script Schedule Setting"
      title={
        <Title level={3} /*style={{ backgroundColor: "lightgray" }}*/>
          Script Schedule Setting
        </Title>
      }
      style={{
        width: "60%",
        paddingRight: "50px",
        paddinLeft: "50px",
        paddingBottom: "50px",
        position: "relative",
        margin: 0,
        boxShadow: "0 2px 8px rgba(0, 0, 0, 0.26)",
        borderRadius: "6px",
        padding: "1rem",
        overflow: "hidden",
        background: "white",
        overflowY: "auto",
        //backgroundColor: "lightgray",
      }}
    >
      {/* <Title level={3} style={{ backgroundColor: "lightgray" }}>
        Script Schedule Setting
      </Title> */}
      <div style={{ display: "flex" }}>
        <h3 style={{ flexGrow: 0.5 }}>Department</h3>
        <h3 style={{ flexGrow: 0.5 }}>Group</h3>
      </div>
      <div style={{ marginBottom: "7%", display: "flex" }}>
        <Select
          placeholder="department"
          size="large"
          //defaultValue="lucy"
          style={{ flexGrow: 0.5, border: "0.1px solid lightgray" }}
          onChange={handleDep}
          value={department}
          //bordered={true}
        >
          {groupData.map((x) => (
            <Option value={x.departmentname}>{x.departmentname}</Option>
          ))}
        </Select>
        <Select
          placeholder="group"
          size="large"
          //defaultValue="lucy"
          style={{ flexGrow: 0.5, border: "0.1px solid lightgray" }}
          onChange={handleGrp}
          value={group}
          //bordered={true}
        >
          {groupData
            .filter((x) => x.departmentname === department && department !== "")
            .map((x) => x.grouplist.map((y) => <Option value={y}>{y}</Option>))}
        </Select>
        {/* {groupData.map((x) => (
          <Select
            placeholder=""
            size="large"
            //defaultValue="lucy"
            style={{ width: 120 }}
            onChange={handleChange}
            //value={x.}
          >
            {x.grouplist.map((y) => (
              <Option value={y}>{y}</Option>
            ))}
          </Select>
        ))} */}

        {/* <Input
          size="large"
          addonBefore="Department"
          //placeholder="Please input description name"
          //defaultValue={data.length !== 0 ? data[0].Description : ""}
          //value={descriptName}
          onChange={(e) => setDescriptName(e.target.value)}
        /> */}
        {/* <Input
          size="large"
          addonBefore="Group"
          //placeholder="Please input description name"
          //defaultValue={data.length !== 0 ? data[0].Description : ""}
          //value={descriptName}
          onChange={(e) => setDescriptName(e.target.value)}
        /> */}
      </div>
      <h3>Description Name</h3>
      <div style={{ marginBottom: "7%" }}>
        <Input
          size="large"
          addonBefore="Description Name"
          placeholder="Please input description name"
          //defaultValue={data.length !== 0 ? data[0].Description : ""}
          value={descriptName}
          onChange={(e) => setDescriptName(e.target.value)}
        />
      </div>
      <h3>Frequency</h3>
      <div style={{ marginBottom: "7%" }}>
        <Radio.Group onChange={onChange} value={value} size="large">
          <Radio style={{ fontSize: "16px" }} value={"By day"}>
            By Day
          </Radio>
          <Radio style={{ fontSize: "16px" }} value={"By week"}>
            By Week
          </Radio>
          <Radio style={{ fontSize: "16px" }} value={"By month"}>
            By Month
          </Radio>
        </Radio.Group>
      </div>
      <h3>Time Setting</h3>
      <div style={{ marginBottom: "5%" }}>
        {value === "By day" && (
          <TimePickerStyle>
            <TimePicker
              style={{ flexBasis: "40%" }}
              // defaultValue={moment("12:00", format)}
              // format={format}
              // onChange={handleOnTime}
              // value={time}
              //defaultValue={moment(time, format)}
              format={format}
              onChange={handleOnTime}
              value={moment(time, format)}
              size="large"
            />
          </TimePickerStyle>
        )}
        {value === "By week" && (
          <TimePickerStyle>
            <FontStyle>&nbsp;&nbsp;</FontStyle>
            <Select
              mode="multiple"
              placeholder=""
              //defaultValue="mon"
              value={dayIndex}
              style={{ flexBasis: "50%", border: "1px solid #d9d9d9" }}
              onChange={handleChange}
              size="large"
              optionLabelProp="label"
            >
              <Option value="1" label="Mon.">
                Monday (Mon.)
              </Option>
              <Option value="2" label="Tue.">
                Tuesday (Tue.)
              </Option>
              <Option value="3" label="Wed.">
                Wednesday (Wed.)
              </Option>
              <Option value="4" label="Thur.">
                Thursday (Thur.)
              </Option>
              <Option value="5" label="Fri.">
                Friday (Fri.)
              </Option>
              <Option value="6" label="Sat.">
                Saturday (Sat.)
              </Option>
              <Option value="7" label="Sun.">
                Sunday (Sun.)
              </Option>
            </Select>
            &nbsp;&nbsp;
            <TimePicker
              style={{ flexBasis: "35%" }}
              //defaultValue={moment(time, format)}
              format={format}
              onChange={handleOnTime}
              value={moment(time, format)}
              // value={moment(time, format)}
              size="large"
            />
          </TimePickerStyle>
        )}
        {value === "By month" && (
          <div
            style={{
              marginBottom: "3%",
              display: "flex",
              flexWrap: "nowrap",
            }}
          >
            <FontStyle>&nbsp;&nbsp;</FontStyle>
            <Select
              mode="multiple"
              //defaultValue="mon"
              style={{ flexBasis: "45%", border: "1px solid #d9d9d9" }}
              onChange={handleChange}
              size="large"
              optionLabelProp="label"
              value={dayIndex}
            >
              {children}
            </Select>
            <FontStyle>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</FontStyle>
            <TimePicker
              style={{ flexBasis: "35%" }}
              // defaultValue={moment("12:00", format)}
              // format={format}
              // onChange={handleOnTime}
              // value={time}
              //defaultValue={moment(time, format)}
              format={format}
              onChange={handleOnTime}
              value={moment(time, format)}
              size="large"
            />
          </div>
        )}
      </div>
      <footer style={{ float: "right" }}>
        <Button
          size="large"
          onClick={() => history.push("/scheduler")}
          style={{
            backgroundColor: "gray",
            borderRadius: "5px",
            color: "white",
          }}
        >
          Cancel
        </Button>
        {/* <Link to="/scheduler" onClick={() => handleOnSave()} > */}
        <Button
          size="large"
          key="3"
          onClick={() => handleOnSave()}
          style={{
            backgroundColor: "#1CC153",
            borderRadius: "5px",
            color: "white",
          }}
          disabled={
            type === "add" && selectedScript === "" && descriptName === ""
          }
        >
          {type === "edit" ? "Save" : "Add Schedule"}
        </Button>
        {/* </Link> */}
        {/*type === "edit" ? (
          <Button
            size="large"
            key="3"
            style={{
              backgroundColor: "#1CC153",
              borderRadius: "5px",
              color: "white",
            }}
            onClick={() => handleOnSave()}
          >
            Save
          </Button>
        ) : (
          <Button
            size="large"
            key="3"
            style={{
              backgroundColor: "#1CC153",
              borderRadius: "5px",
              color: "white",
            }}
            onClick={() => handleOnSave()}
          >
            Add Schedule
          </Button>
        )*/}
      </footer>
    </Card>
  );
};

export default ScriptSetting;
