import React, { useState } from "react";
import { Link } from "react-router-dom";
import MainHeader from "./MainHeader";
import NavLinks from "./NavLinks";
import "./MainNavigation.css";
import logo from "../../../images/konka_logo.png";
//import logo from "../../../images/fst-logo.png";
import { createGlobalStyle } from "styled-components";
//import imgLight from "../../../images/back26-unsplash.jpg";
import imgLight from "../../../images/back24-unsplash.jpg";
// import imgDark from "../../../images/back-unsplash.jpg";

const GlobalStyle = createGlobalStyle`
  body {
    background:url(${(props) => props.imgUrl});
    background-size:cover;
    background-color:#FAFAFA
  }
`;

const MainNavigation = ({ login }) => {
  const [mode, setMode] = useState("light");
  return (
    <React.Fragment>
      {!login ? (
        <GlobalStyle imgUrl={imgLight} />
      ) : (
        <GlobalStyle imgUrl={null} />
      )}

      {/* {mode === "light" ? (
        <GlobalStyle imgUrl={imgLight} />
      ) : (
        <GlobalStyle imgUrl={imgDark} />
      )} */}
      {login && (
        <MainHeader mode={mode}>
          <h1 className="main-navigation__title">
            <Link to="/">
              <img src={logo} alt="" style={{ width: "15%", float: "left" }} />
              {/* <img src={logo} alt="" style={{ width: "50%", float: "left" }} /> */}
            </Link>
          </h1>
          <nav className="main-navigation__header-nav">
            <NavLinks mode={mode} setMode={setMode} />
          </nav>
        </MainHeader>
      )}
    </React.Fragment>
  );
};

export default MainNavigation;
