import { useEffect, useState, useCallback } from "react";

export const useGetData = (apiInstance) => {
  const [fetchData, setFetchData] = useState(null);
  const fetchingDataAsync = useCallback(async () => {
    try {
      const res = await apiInstance.get("");
      setFetchData(res.data);
    } catch (err) {
      throw new Error(err);
    }
  }, [apiInstance]);
  useEffect(() => {
    fetchingDataAsync();
  }, [fetchingDataAsync]);

  return [fetchData, fetchingDataAsync, setFetchData];
};
