import axios from "axios";
import { hostUrl } from "./host-url";

//#region Recent Reports

//  //get
export const getRecentReportsPicsApi = (day) =>
  axios.create({
    baseURL: `${hostUrl}/RecentReports/${day}`,
  });

//imageHtml //get
export const getRecentReportsDetailApi = (permissions, reportname) =>
  axios.create({
    baseURL: `${hostUrl}/RecentReports/detail/${permissions}/${reportname}`,
  });

