import React, { useState, useEffect, useContext } from "react";
import PageHeaders from "../components/PageHeaders";
import DataTable from "../components/DataTable";
import DetailTable from "../components/DetailTable";
import ControlBar from "../components/ControlBar";
import { getReportListDataApi, refreshTokenApi } from "../../api-list/api-list";
import { AuthContext } from "../../shared/contexts/auth-context";
import {
  getAuthToken,
  removeAuthToken,
  setAuthToken,
} from "../../shared/util/getAuth";
import { message } from "antd";
import moment from "moment";
import { useHistory } from "react-router";
const dateFormat = "YYYYMMDD";

const Report = ({
  setUserData,
  setIsLoggedIn,
  setUserRole,
  setUserDepartment,
}) => {
  const auth = useContext(AuthContext);
  const [listData, setListData] = useState([]);
  const [changeData, setChangeData] = useState([]);
  const [startTime, setStartTime] = useState(
    moment().subtract(30, "days").format(dateFormat)
  );
  const [endTime, setEndTime] = useState(moment().format(dateFormat));
  const history = useHistory();
  // const [fetchData] = useGetData(
  //   getReportListDataApi("Admin", 20010701, 20211123)
  // );

  // let reportList = [];
  // if (fetchData) {
  //   reportList = fetchData.data;
  // }
  const [nongId, setNongId] = useState([]);

  useEffect(() => {
    const token = getAuthToken();
    const config = {
      headers: {
        authorization: `Bearer ${token}`,
      },
    };
    const getReportsList = async () => {
      //Event.preventDefault();
      try {
        const res = await getReportListDataApi(
          //auth.Role,
          startTime,
          endTime
        ).get("", config);

        if (res.data) {
          // console.log(res.data);
          setNongId([]);
          setChangeData([]);

          // const arr = res.data.data.map((x, idx) => {
          //   x.key = idx;
          //   return x;
          // });
          //console.log(arr);
          setListData(res.data.data);
          //return res.data.Path;
        }
      } catch (err) {
        console.log(err);
        if (err.response.status === 401) {
          removeAuthToken();
          message.info("");
          setUserData(null);
          setIsLoggedIn(false);
          setUserDepartment(null);
          setUserRole(null);
          history.push("/");
        }
        /*
        if (err.response.status === 401) {
          try {
            await refreshTokenApi
              .post("", { refreshtoken: auth.UserData.RefreshToken }, config)
              .then((res) => {
                //console.log(res.data);
                setAuthToken(res.data.AccessToken);
              })
              .catch((errs) => {
                //console.log(errs.response);
                if (errs.response.status === 401) {
                  removeAuthToken();
                  message.info("");
                  setUserData(null);
                  setIsLoggedIn(false);
                  setUserRole(null);
                  history.push("/");
                  //return Promise.reject(err);
                }
              });
          
          } catch (_err) {
            console.log(_err);
          }
        }*/
      }
    };
    getReportsList();
  }, [
    startTime,
    endTime,
    auth,
    history,
    setUserData,
    setIsLoggedIn,
    setUserRole,
    setUserDepartment,
  ]);
  //console.log(changeData);

  return (
    <div style={{ width: "90%" }}>
      <div style={{ display: "flex" }}>
        <PageHeaders title={"Report"} back={false} />
        {/* <Divider style={{ marginBottom: "30px", width: "50%" }} /> */}
        {/* <Divider style={{ margin: "30px", borderColor: "lightgrey" }} /> */}
        <ControlBar
          startTime={startTime}
          setStartTime={setStartTime}
          endTime={endTime}
          setEndTime={setEndTime}
        />
      </div>
      <DataTable
        data={listData}
        setChangeData={setChangeData}
        nongId={nongId}
        setNongId={setNongId}
      />
      <DetailTable
        changeData={changeData}
        startTime={startTime}
        endTime={endTime}
      />
    </div>
  );
};

export default Report;
