import React, { useState } from "react";
import { Pagination } from "antd";
import PageHeaders from "../components/PageHeaders";
import ReportPage from "../components/ReportPage";
import RecentReportControlBar from "../components/RecentReportControlBar";

const RecentReport = ({
  repoData,
  setRecentReportDays,
  setFetchData,
  recentReportDays,
  setUserData,
  setIsLoggedIn,
  setUserRole,
  setUserDepartment,
}) => {
  //console.log(repoData);
  //const [minValue, setMinValue] = useState(0);
  //const [maxValue, setMaxValue] = useState(6);

  //const [loading, setLoading] = useState(false);
  //const imageRef = useRef(null);
  //const [size, setSize] = useState({});
  // console.log(size);

  const [changeData, setChangeData] = useState([]);
  const [minValue, setMinValue] = useState(0);
  const [maxValue, setMaxValue] = useState(6);
  const numEachPage = 6;
  const total = repoData.length / numEachPage + 1;
  const handleChange = (value) => {
    setMinValue((value - 1) * numEachPage);
    setMaxValue(value * numEachPage);
  };

  return (
    <div style={{ width: "90%" }}>
      <div style={{ display: "flex" }}>
        <PageHeaders title={"Recent Reports"} back={false} />
        <RecentReportControlBar
          changeData={changeData}
          setRecentReportDays={setRecentReportDays}
          setFetchData={setFetchData}
          recentReportDays={recentReportDays}
          //fetchingDataAsync={fetchingDataAsync}
          setUserData={setUserData}
          setIsLoggedIn={setIsLoggedIn}
          setUserRole={setUserRole}
          setUserDepartment={setUserDepartment}
        />
      </div>
      {/* <button onClick={() => setLoading(!loading)}>Click For Test!</button> */}
      {/* <Divider style={{ margin: "30px", borderColor: "white" }} /> */}
      <ReportPage repoData={repoData} minValue={minValue} maxValue={maxValue} />
      <Pagination
        defaultCurrent={1}
        total={total}
        //defaultPageSize={numEachPage} //default size of page
        onChange={handleChange}
        style={{ textAlign: "center", padding: "5%" }}
      />
    </div>
  );
};

export default RecentReport;
