import React, { useState } from "react";
import { Tabs, Button, Modal, Input, message } from "antd";
import { UsergroupAddOutlined } from "@ant-design/icons";
import GroupTabContent from "./GroupTabContent";
import WithSpinner from "../../shared/components/UIELements/WithSpinner";
import { postAddDepartmentApi, getGroupInfoApi } from "../../api-list/api-list";
import {
  getAuthToken,
  setAuthToken,
  removeAuthToken,
} from "../../shared/util/getAuth";
import { useHistory } from "react-router-dom";
const { TabPane } = Tabs;
const GroupTabContentWithSpinner = WithSpinner(GroupTabContent);

const GroupClassify = ({
  dep,
  groupInfo,
  setGroupInfo,
  pane,
  setPane,
  setUserData,
  setIsLoggedIn,
  setUserRole,
  setUserDepartment,
  department,
}) => {
  //console.log(dep, groupInfo);
  //const [newTabIndex, setNewTabIndex] = useState(0);
  //   let panes = /*new Array(dep).fill(null)*/ groupInfo
  //     .filter((y) => y.departmentname !== "Admin")
  //     .map((x, index) => {
  //       const id = String(index + 1);
  //       return {
  //         title: x.departmentname, //`Tab ${id}`,
  //         //content: `Content of Tab Pane ${id}`,
  //         key: id,
  //       };
  //     });
  const history = useHistory();
  const [activeKey, setActiveKey] = useState(
    pane.length !== 0 ? pane[0].key : 0
  );
  //const [pane, setPane] = useState(panes);
  const [open, setOpen] = useState(false);
  const [departmentName, setDepartmentName] = useState("");

  const onChange = (activeKey) => {
    //console.log(activeKey);
    setActiveKey(activeKey);
    //this.setState({ activeKey });
  };

  const onEdit = (targetKey, action) => {
    //console.log(action, targetKey);
    remove(targetKey);
    //this[action](targetKey);
  };

  /*const add = () => {
    const p = [...pane];
    //const idx=newTabIndex
    let key = `newTab${p.length + 1}`;
    p.push({ title: "New Tab", content: "New Tab Pane", key: key });

    setPane(p);
    setActiveKey(key);
    //this.setState({ panes, activeKey });
  };*/

  const remove = (targetKey) => {
    // const key = activeKey.toString();
    let lastIndex;
    pane.forEach((pane, i) => {
      if (pane.key === targetKey) {
        lastIndex = i - 1;
      }
    });
    const panes = pane.filter((pane) => pane.key !== targetKey);
    if (panes.length && activeKey === targetKey) {
      if (lastIndex >= 0) {
        //key = panes[lastIndex].key;
        setActiveKey(panes[lastIndex].key);
      } else {
        //key = panes[0].key;
        setActiveKey(panes[0].key);
      }
    }
    setPane(panes);
    //setActiveKey(key);
    //this.setState({ panes, activeKey });
  };

  const modalConfirm = () => {
    const tempPane = [...pane];
    let key = `newTab${tempPane.length + 1}`;
    tempPane.push({
      title: departmentName,
      content: departmentName + "Pane",
      key: key + "dep",
    });

    let addJson = { grouplist: [], departmentname: departmentName };
    postGroupInfoList(addJson);
    //setPane(tempPane);
    //setActiveKey(key);
    modalClose();
  };
  const modalClose = () => {
    setOpen(false);
    setDepartmentName("");
  };

  const postGroupInfoList = async (addJson) => {
    const token = getAuthToken();
    const config = {
      headers: {
        authorization: `Bearer ${token}`,
      },
    };

    try {
      const res = await postAddDepartmentApi.post("", addJson, config);
      if (res.data.result) {
        //console.log(res.data);
        getGroupInfoList();
        //return res.data.Path;
      }
    } catch (err) {
      console.log(err);
      removeAuthToken();

      setUserData(null);
      setIsLoggedIn(false);
      setUserRole(null);
      setUserDepartment(null);
      message.info("");
      history.push("/");
      //history.push("/auth");
      //auth.isLoggedIn
      //auth.logout();
      //RefreshToken("test", "test");
      /*setUserData(null);
      setIsLoggedIn(false);
      setUserRole(null);
      history.push("/");*/

      //   if (err.response.status === 401) {
      //     try {
      //       await refreshTokenApi
      //         .post("", { refreshtoken: auth.UserData.RefreshToken }, config)
      //         .then((res) => {
      //           //console.log(res.data);
      //           setAuthToken(res.data.AccessToken);
      //         })
      //         .catch((errs) => {
      //           //console.log(errs.response);
      //           if (errs.response.status === 401) {
      //             removeAuthToken();
      //             message.info("");
      //             setUserData(null);
      //             setIsLoggedIn(false);
      //             setUserRole(null);
      //             history.push("/");
      //             //return Promise.reject(err);
      //           }
      //         });
      //       /*if (res) setAuthToken(res.data.AccessToken);
      //     else setAuthToken("");*/
      //     } catch (_err) {
      //       console.log(_err);
      //     }
      //   }
    }
  };

  const getGroupInfoList = async () => {
    const token = getAuthToken();
    const config = {
      headers: {
        authorization: `Bearer ${token}`,
      },
    };
    try {
      const res = await getGroupInfoApi.get("", config);
      if (res.data) {
        let panes = /*new Array(dep).fill(null)*/ res.data.departmentlist
          .filter((y) => y.departmentname !== "Admin")
          .map((x, index) => {
            const id = String(index + 1);
            return {
              title: x.departmentname, //`Tab ${id}`,
              //content: `Content of Tab Pane ${id}`,
              key: id,
            };
          });
        setPane(panes);
        setGroupInfo(res.data.departmentlist);
        //return res.data.Path;
      }
    } catch (err) {
      console.log(err.response);
      //message.info("");
      //history.push("/auth");
      //auth.isLoggedIn
      //auth.logout();
      //RefreshToken("test", "test");
      /*setUserData(null);
            setIsLoggedIn(false);
            setUserRole(null);
            history.push("/");*/

      // if (err.response.status === 401) {
      //   try {
      //     await refreshTokenApi
      //       .post("", { refreshtoken: auth.UserData.RefreshToken }, config)
      //       .then((res) => {
      //         //console.log(res.data);
      //         setAuthToken(res.data.AccessToken);
      //       })
      //       .catch((errs) => {
      //         //console.log(errs.response);
      //         if (errs.response.status === 401) {
      //           removeAuthToken();
      //           message.info("");
      //           setUserData(null);
      //           setIsLoggedIn(false);
      //           setUserRole(null);
      //           history.push("/");
      //           //return Promise.reject(err);
      //         }
      //       });
      //     /*if (res) setAuthToken(res.data.AccessToken);
      //       else setAuthToken("");*/
      //   } catch (_err) {
      //     console.log(_err);
      //   }
      // }
    }
  };
  const slot = React.useMemo(() => {
    if (department !== "Admin") return null;
    else return <Button onClick={() => setOpen(true)}>ADD</Button>;
  }, [department]);

  return (
    <div
      style={{
        width: "90%",
        margin: "auto",
      }}
    >
      {/* <div style={{ marginBottom: 16 }}>
        <Button onClick={() => setOpen(true)}>ADD</Button>
      </div> */}
      <Tabs
        style={{
          borderStyle: "solid",
          padding: "20px",
          borderColor: "lightgray",
          height: "65vh",
          borderRadius: "3px",
          borderWidth: "0.5px",
          boxShadow: "0 3px 7px rgba(0, 0, 0, 0.3)",
          overflowY: "auto",
        }}
        hideAdd
        size="large"
        onChange={onChange}
        activeKey={activeKey}
        //type="editable-card"
        //type="card"
        onEdit={onEdit}
        tabPosition="left"
        tabBarExtraContent={
          slot
          //department === "Admin" ?right: <Button onClick={() => setOpen(true)}>ADD</Button>:null,
        }
      >
        {pane.map((pane) => (
          <TabPane tab={pane.title} key={pane.key} closable={false}>
            {/* {pane.content} */}
            <GroupTabContentWithSpinner
              isLoading={!!!groupInfo}
              title={pane.title}
              // dep={dep.filter((x) => x.dep === pane.title)[0]}
              dep={groupInfo.filter((x) => x.departmentname === pane.title)[0]}
              setGroupInfo={setGroupInfo}
              setUserData={setUserData}
              setIsLoggedIn={setIsLoggedIn}
              setUserRole={setUserRole}
              setUserDepartment={setUserDepartment}
            />
          </TabPane>
        ))}
      </Tabs>
      <Modal
        title="Add Department"
        width={400}
        visible={open}
        onOk={modalConfirm}
        //okButtonProps=
        //   <Popconfirm
        //     placement="topLeft"
        //     title={""}
        //     onConfirm={modalConfirm}
        //     okText="Yes"
        //     cancelText="No"
        //   ></Popconfirm>
        // }
        onCancel={modalClose}
        closable={false}
      >
        <Input
          size="large"
          placeholder="Please input new department name."
          prefix={<UsergroupAddOutlined />}
          value={departmentName}
          onChange={(e) => setDepartmentName(e.target.value)}
        />
      </Modal>
    </div>
  );
};

export default GroupClassify;
