import React from "react";
import { Button } from "antd";
import { NavLink } from "react-router-dom";

const AuthControlBar = ({ scheduleData }) => {
  const target = [];
  const dep = [];

  scheduleData.map((x, idx) => {
    x.key = idx;
    x.title = x.account;
    if (x.groupchosen) {
      target.push(x.key);
    }

    const depIdx = dep.findIndex((y) => y.dep === x.department);
    if (depIdx === -1) {
      dep.push({ dep: x.department, group: [x.group] });
    } else {
      const groupIdx = dep[depIdx].group.findIndex((y) => y === x.group);
      if (groupIdx === -1) {
        dep[depIdx].group.push(x.group);
      }
    }
    return x;
  });

  return (
    <div
      style={{
        float: "right",
        margin: "auto auto 3em auto",
        justifyContent: "space-between",
        marginRight: "5%",
      }}
    >
      <NavLink
        to={{
          pathname: "/groupsetting",
          state: dep,
        }}
      >
        <Button>Group Setting</Button>
      </NavLink>
      <NavLink
        to={{
          pathname: "/authsetting",
          state: {
            dep: dep,
            target: target,
            userInfo: scheduleData,
          },
        }}
      >
        <Button>Auth Setting</Button>
      </NavLink>
    </div>
  );
};

export default AuthControlBar;
