import React, { useState, useContext } from "react";
import Card from "../../shared/components/UIELements/Card";
import Input from "../../shared/components/FormElements/Inputs";
import Button from "../../shared/components/FormElements/Button";
import { AuthContext } from "../../shared/contexts/auth-context";
import { useForm } from "../../shared/hooks/form-hooks";
import { loginApi } from "../../api-list/api-list";
import { Spinner } from "../../shared/components/UIELements/WithSpinner";
import {
  VALIDATOR_MINLENGTH,
  VALIDATOR_REQUIRE,
} from "../../shared/util/validators";
import { Typography } from "antd";
//import fstLogo from "../../images/fst-logo.png";
import { Link } from "react-router-dom";
import { setAuthToken, removeAuthToken } from "../../shared/util/getAuth";
import konkaLogo from "../../images/konka.jpeg";
import fstLogo from "../../images/logo.png";
import { getMe } from "./Authenticated";
import "./Auth.css";
import { useHistory } from "react-router-dom";
