const Auth = () => {
  const history = useHistory();
  const auth = useContext(AuthContext);
  const [isLoginning, setIsLoginning] = useState(false);
  const [errMsg, setErrMsg] = useState("");

  const [formState, inputHandler] = useForm(
    {
      account: {
        value: "",
        isValid: false,
      },
      password: {
        value: "",
        isValid: false,
      },
    },
    false
  );
  const authSubmitHandler = async (e) => {
    e.preventDefault();

    const { account, password } = formState.inputs;
    const loginInfo = {
      AccountName: account.value,
      password: password.value,
    };
    try {
      setIsLoginning(true);
      const res = await loginApi.post("", loginInfo);
      if (res && res.data) {
        //auth.login(res.data.userRole, res.data.user);
        if (res.data.status !== 200) {
          setErrMsg("");
        }
        setAuthToken(res.data.AccessToken);
        getMe().then((response) => {
          //console.log(response);
          if (response.status !== 200) {
            //  getMe()  token 
            //setAuthToken("");
            removeAuthToken();
            auth.logout();

            //setErrMsg(response.toString());
          }
          //setUser(response.data);
          else {
            auth.login(
              res.data.Department,
              res.data.Role,
              res.data.AccountName,
              res.data
            );
            //setIsLoginning(false);
            //
            history.push("/");
          }
        });
        //const toLocal = JSON.stringify(res.data);
        //localStorage.setItem("user", res.data.AccessToken);

        // if (res.data.accessToken) {
        //   localStorage.setItem("user", JSON.stringify(res.data));
        //   auth.login(res.data.userRole, res.data.user);
        // }
      } else {
        setErrMsg("");
        setTimeout(() => {
          setErrMsg("");
        }, 3000);
        setIsLoginning(false);
      }
    } catch (err) {
      console.log(err);
      setErrMsg("");
      setTimeout(() => {
        setErrMsg("");
      }, 3000);
      /* return () => {
        clearTimeout(timeoutTag);
      };*/
      setIsLoginning(false);
    }
  };

  const _authSubmitHandler = async (e) => {
    e.preventDefault();
    const { account, password } = formState.inputs;
    const loginInfo = {
      user: account.value,
      password: password.value,
    };

    if (account.value === "user" && password.value === "user") {
      setIsLoginning(false);
      auth.login("Admin", loginInfo.user);
    } else {
      setErrMsg("");
      setTimeout(() => {
        setErrMsg("");
      }, 3000);
      setIsLoginning(false);
    }
    // try {
    //   setIsLoginning(true);
    //   const res = await loginApi.post("/", loginInfo);
    //   if (res && res.data.userRole) {
    //     setIsLoginning(false);
    //     auth.login(res.data.userRole);
    //   } else {
    //     setErrMsg("");
    //     setTimeout(() => {
    //       setErrMsg("");
    //     }, 3000);
    //     setIsLoginning(false);
    //   }
    // } catch (err) {
    //   console.log(err);
    //   setErrMsg("");
    //   setTimeout(() => {
    //     setErrMsg("");
    //   }, 3000);
    //   setIsLoginning(false);
    // }
  };

  return (
    <Card className="authentication">
      <img
        src={konkaLogo}
        //src={fstLogo}
        alt=""
        style={{ width: "50%", paddingTop: "5%" /* height: "10%" */ }}
      />
      {/* <Avatar
        //style={{ backgroundColor: "#ff1744", margin: "5%" }}
        style={{ backgroundColor: "transparent", margin: "3%" }}
        //icon={<LockOutlined />}
        icon={<img src={fstLogo} alt="fireSpot" />}
        size={50}
      /> */}
      <Typography component="h3" variant="h5">
        Engineering Data Analysis
      </Typography>
      {/* <Typography component="h2" variant="h5">
        
      </Typography> */}
      <h2></h2>
      {errMsg && <h4 className="authentication__error">{errMsg}</h4>}
      {isLoginning ? (
        <Spinner />
      ) : (
        <form className="authentication__form" onSubmit={authSubmitHandler}>
          <Input
            id="account"
            type="text"
            //label=""
            element="input"
            validators={[VALIDATOR_REQUIRE(), VALIDATOR_MINLENGTH(4)]}
            errorText=""
            onInput={inputHandler}
            placeholder=""
            //autofocus
          />

          <Input
            id="password"
            type="password"
            //label=""
            element="input"
            validators={[VALIDATOR_REQUIRE(), VALIDATOR_MINLENGTH(4)]}
            errorText=""
            onInput={inputHandler}
            style={{ width: "100%" }}
            placeholder=""
          />

          {/* <Input.Password
            placeholder="input password"
            iconRender={(visible) =>
              visible ? <EyeTwoTone /> : <EyeInvisibleOutlined />
            }
            
          /> */}

          <Button
            className="authentication__form__login"
            type="submit"
            //style={{ width: "100%" }}
            disabled={!formState.isValid}
          >
            
          </Button>
          <Link to="/register">
            <p></p>
          </Link>
        </form>
      )}
    </Card>
  );
};

export default Auth;
