import React, { useReducer, useEffect } from "react";
import { Select } from "antd";

import styled from "styled-components";
//import { validate } from "../../util/validators";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBriefcase } from "@fortawesome/free-solid-svg-icons";
const { Option } = Select;
const icon = (props) => (
  <FontAwesomeIcon
    style={{ margin: "0.5rem 0.5rem" }}
    icon={faBriefcase}
    size="1x"
  />
);

// styles
const PrefixListIcon = styled(icon)`
  padding: 8px;
  border: 1px solid #d9d9d9;
  //border-right: none;
  border-bottom-left-radius: 2px;
  border-top-left-radius: 2px !important;
  background: #fff;
  //padding: 0.15rem 0.25rem;
`;

const InputReducer = (state, action) => {
  switch (action.type) {
    case "CHANGE":
      return {
        ...state,
        value: action.val,
        isValid: true, //validate(action.val, action.validators),
      };

    default:
      return state;
  }
};
//<FontAwesomeIcon icon={faBriefcase} size="1x" />

const InputGroup = (props) => {
  const [inputState, dispatch] = useReducer(InputReducer, {
    value: props.initialValue || undefined,
    //isTouched: false,
    isValid: true, //props.initialValid || false,
  });
  const { id, onInput, className } = props;
  const { value, isValid } = inputState;

  useEffect(() => {
    onInput(id, value, isValid);
  }, [id, isValid, onInput, value]);

  const changeHandler = (depart) => {
    //dispatch action obj
    dispatch({
      type: "CHANGE",
      val: depart,
      validators: true,
    });
  };

  return (
    // <div
    //   className={`form-control ${
    //     !inputState.isValid && inputState.isTouched && "form-control--invalid"
    //   } ${className && className}`}
    // >
    //   <label htmlFor={props.id} style={{ textAlign: "left" }}>
    //     {props.label}
    //   </label>
    //   {element}
    //   {!inputState.isValid && inputState.isTouched && <p>{props.errorText}</p>}
    // </div>
    <div
      style={{
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        width: "100%",
        margin: "1.5rem 0",
        padding: "3px 6px",
        border: "1px solid #d9d9d9",
        //borderRight: "none",
        borderBottomLeftRadius: "2px",
        borderTopLeftRadius: "2px !important",
        background: "#fff",
        //padding: 0.15rem 0.25rem;
      }}
    >
      <PrefixListIcon />
      <Select
        id="department"
        style={{
          width: "100%",
          textAlign: "left",
          fontSize: "0.98rem",
          borderLeft: "none",
          //padding: "0.15rem 0.25rem",
        }}
        value={inputState.value}
        placeholder=""
        onChange={changeHandler}
        //defaultValue="Home"
      >
        {props.groupData.map((x) => (
          <Option value={x.departmentname}>{x.departmentname}</Option>
        ))}
        {/* <Option value="CIM">CIM</Option>
        <Option value="QA">QA</Option> */}
      </Select>
    </div>
  );
};

export default InputGroup;
