const Authority = ({
  setUserData,
  setIsLoggedIn,
  setUserRole,
  setUserDepartment,
}) => {
  const [changeData, setChangeData] = useState([]);
  const [useInfo, setUseInfo] = useState();
  const auth = useContext(AuthContext);
  const history = useHistory();

  useEffect(() => {
    const token = getAuthToken();
    const config = {
      headers: {
        authorization: `Bearer ${token}`,
      },
    };
    const getUserInfoList = async () => {
      //Event.preventDefault();
      try {
        const res = await getUserInfoApi.get("", config);
        if (res.data) {
          // console.log(res.data);
          let userArr = res.data.users.map((x, idx) => {
            x.key = idx;
            return x;
          });
          setUseInfo(userArr);
          //return res.data.Path;
        }
      } catch (err) {
        console.log(err);
        //message.info("");
        //history.push("/auth");
        //auth.isLoggedIn
        //auth.logout();
        //RefreshToken("test", "test");
        /*setUserData(null);
        setIsLoggedIn(false);
        setUserRole(null);
        history.push("/");*/
        if (err.response.status === 401) {
          try {
            await refreshTokenApi
              .post("", { refreshtoken: auth.UserData.RefreshToken }, config)
              .then((res) => {
                //console.log(res.data);
                setAuthToken(res.data.AccessToken);
              })
              .catch((errs) => {
                //console.log(errs.response);
                if (errs.response.status === 401) {
                  removeAuthToken();
                  message.info("");
                  setUserData(null);
                  setIsLoggedIn(false);
                  setUserDepartment(null);
                  setUserRole(null);
                  history.push("/");
                  //return Promise.reject(err);
                }
              });
            /*if (res) setAuthToken(res.data.AccessToken);
          else setAuthToken("");*/
          } catch (_err) {
            console.log(_err);
          }
        }
      }
    };
    getUserInfoList();
  }, [
    setUserData,
    history,
    setIsLoggedIn,
    setUserRole,
    setUserDepartment,
    auth.UserData.RefreshToken,
  ]);

  // const checkData = memberList
  //   .map((x) => ({ [x.userId]: x.check }))
  //   .reduce((a, b) => {
  //     return Object.assign(a, b);
  //   });

  // const [checked, setChecked] = useState(checkData);

  // const handleOnClick = (e, id) => {
  //   setChecked({ ...checked, [id]: e.target.checked });
  // };

  return (
    <div style={{ width: "90%" }}>
      <div style={{ display: "flex" }}>
        <PageHeaders title={"Authority"} back={false} />
        <AuthControlBarWithSpinner
          isLoading={!!!useInfo}
          scheduleData={useInfo}
        />
        {/* <CardComponent
        color={"info"}
        title={"test_component"}
        content={"test_component"}
        styles={"result-fixed"}
      /> */}
      </div>
      <DataTableWithSpinner
        isLoading={!!!useInfo}
        setChangeData={setChangeData}
        scheduleData={useInfo}
        setUseInfo={setUseInfo}
        setUserData={setUserData}
        setIsLoggedIn={setIsLoggedIn}
        setUserRole={setUserRole}
        setUserDepartment={setUserDepartment}
      />

      {/* <form
        style={{
          display: "flex",
          textAlign: "center",
        }}
      >
        <table
          style={{
            borderWidth: "1px",
            boxShadow: "0 2px 8px rgba(0, 0, 0, 0.26)",
            borderCollapse: "collapse",
            borderRadius: "3px",
            alignItems: "center",
            margin: "auto",
            // display: "flex", textAlign: "center"
          }}
        >
          <thead>
            <tr style={{ backgroundColor: "lightgray" }}>
              <THStyle>User ID</THStyle>
              <THStyle>User Role</THStyle>
              <THStyle>Check</THStyle>
            </tr>
          </thead>
          <tbody>
            {memberList.map((item) => (
              <tr>
                <TDStyle>{item.id}</TDStyle>
                <TDStyle>{item.role}</TDStyle>
                <TDStyle>
                  {checked[item.id] ? (
                    ""
                  ) : (
                    <input
                      type="checkbox"
                      checked={checked[item.id]}
                      defaultChecked={item.check}
                      onClick={(e) => handleOnClick(e, item.id)}
                    />
                  )}
                </TDStyle>
              </tr>
            ))}
          </tbody>
        </table>
         <button type="submit"></button> 
      </form> */}
    </div>
  );
};

export default Authority;
