import React, { useState, useContext, useEffect } from "react";
import { Transfer, message } from "antd";
import {
  postAssigngroupApi,
  getUserInfoApi,
  refreshTokenApi,
} from "../../api-list/api-list";
import { useHistory } from "react-router-dom";
import {
  getAuthToken,
  setAuthToken,
  removeAuthToken,
} from "../../shared/util/getAuth";
import { AuthContext } from "../../shared/contexts/auth-context";

const GroupTransfer = ({
  json,
  chooseGroup,
  setUseInfo,
  chooseDepartment,
  setUserData,
  setIsLoggedIn,
  setUserRole,
  setUserDepartment,
}) => {
  //const [useInfo, setUseInfo] = useState();
  //const [state, setState] = useState({ mockData: [], targetKeys: [] });
  //const [mockData, setMockData] = useState([])
  const history = useHistory();
  const auth = useContext(AuthContext);
  let target = [];
  json.forEach((element) => {
    if (element.groupchosen) {
      target.push(element.key);
    }
  });
  const [targetKeys, setTargetKeys] = useState(target);
  //const getMock = () => {
  // const target = [];
  // const mockData = [];
  // json.forEach((x) => {
  //   if (x.chosen) {
  //     target.push(x.key);
  //   } //else
  //   mockData.push(x.key);
  // });
  let dataSource =
    chooseGroup !== ""
      ? json.filter(
          (x) =>
            (x.group === chooseGroup || x.group === "") &&
            x.department === chooseDepartment &&
            x.role !== "Leader" &&
            x.department !== "Admin"
        )
      : json.filter(
          (x) =>
            x.role !== "Leader" &&
            x.department !== "Admin" &&
            x.department === chooseDepartment
        );
  //console.log(dataSource);
  // for (let i = 0; i < 20; i++) {
  //   const data = {
  //     key: i.toString(),
  //     title: `User${i + 1}`,
  //     //title: `content${i + 1}`,
  //     //description: `description of content${i + 1}`,
  //     chosen: Math.random() * 2 > 1,
  //   };
  //   if (data.chosen) {
  //     targetKeys.push(data.key);
  //   }
  //   mockData.push(data);
  // }
  //console.log(mockData);
  //setState({ ...state, mockData: mockData, targetKeys: targetKeys });
  //};

  // useEffect(() => {
  //   if (chooseDepartment) {
  //   }
  // }, []);

  const handleChange = (targetKeys, direction, moveKeys) => {
    //console.log(targetKeys, direction, moveKeys);
    setTargetKeys(targetKeys);
    moveKeys.forEach((y) => {
      let idx = json.findIndex((x) => x.key === y);
      //Group
      if (direction === "right") {
        if (idx !== -1) {
          let postJson = {
            ...json[idx],
            group: chooseGroup,
            groupchosen: true,
          };
          // console.log(postJson);
          handleTransfer(postJson);
        }
      } else {
        if (idx !== -1) {
          let postJson = { ...json[idx], group: "", groupchosen: false };
          // console.log(postJson);
          handleTransfer(postJson);
        }
      }
    });

    //setState({ ...state, targetKeys: targetKeys });
  };

  const handleTransfer = async (postJson, action, groupchosen) => {
    const token = getAuthToken();
    const config = {
      headers: {
        authorization: `Bearer ${token}`,
      },
    };

    try {
      const res = await postAssigngroupApi.post("", postJson, config);
      if (res.data.result) {
        //console.log(res.data);
        message.info("");
        getUserInfoList();
        //getGroupInfoList();
        //return res.data.Path;
      } else {
        message.warning("");
      }
    } catch (err) {
      console.log(err);

      //message.info("");
      //history.push("/auth");
      //auth.isLoggedIn
      //auth.logout();
      //RefreshToken("test", "test");
      /*setUserData(null);
      setIsLoggedIn(false);
      setUserRole(null);
      history.push("/");*/

      if (err.response.status === 401) {
        try {
          await refreshTokenApi
            .post("", { refreshtoken: auth.UserData.RefreshToken }, config)
            .then((res) => {
              //console.log(res.data);
              setAuthToken(res.data.AccessToken);
            })
            .catch((errs) => {
              //console.log(errs.response);
              if (errs.response.status === 401) {
                removeAuthToken();
                setUserData(null);
                setIsLoggedIn(false);
                setUserDepartment(null);
                setUserRole(null);
                history.push("/");
                message.info("");
                //return Promise.reject(err);
              }
            });
          /*if (res) setAuthToken(res.data.AccessToken);
          else setAuthToken("");*/
        } catch (_err) {
          console.log(_err);
        }
      }
    }
  };

  const getUserInfoList = async () => {
    const token = getAuthToken();
    const config = {
      headers: {
        authorization: `Bearer ${token}`,
      },
    };
    try {
      const res = await getUserInfoApi.get("", config);
      if (res.data) {
        // console.log(res.data);
        let userArr = res.data.users.map((x, idx) => {
          x.key = idx;
          return x;
        });
        setUseInfo(userArr);
        //return res.data.Path;
      }
    } catch (err) {
      console.log(err);

      // if (err.response.status === 401) {
      //   try {
      //     await refreshTokenApi
      //       .post("", { refreshtoken: auth.UserData.RefreshToken }, config)
      //       .then((res) => {
      //         //console.log(res.data);
      //         setAuthToken(res.data.AccessToken);
      //       })
      //       .catch((errs) => {
      //         //console.log(errs.response);
      //         if (errs.response.status === 401) {
      //           removeAuthToken();
      //           message.info("");
      //           setUserData(null);
      //           setIsLoggedIn(false);
      //           setUserRole(null);
      //           history.push("/");
      //           //return Promise.reject(err);
      //         }
      //       });
      //     /*if (res) setAuthToken(res.data.AccessToken);
      //   else setAuthToken("");*/
      //   } catch (_err) {
      //     console.log(_err);
      //   }
      // }
    }
  };

  // const renderFooter = (props) => {
  //   if (direction === "left") {
  //     return (
  //       <Button
  //         size="small"
  //         style={{ float: "left", margin: 5 }}
  //         onClick={this.getMock}
  //       >
  //         Left button reload
  //       </Button>
  //     );
  //   }
  //   return (
  //     <Button
  //       size="small"
  //       style={{ float: "right", margin: 5 }}
  //       onClick={getMock}
  //     >
  //       Right button reload
  //     </Button>
  //   );
  // };

  return (
    <>
      {chooseGroup !== "" ? (
        <Transfer
          style={{ width: "90%", margin: "auto", paddingLeft: "20px" }}
          dataSource={dataSource}
          showSearch
          listStyle={{
            width: "100%",
            height: 450,
          }}
          titles={["", ""]}
          operations={["to right", "to left"]}
          //selectedKeys={}
          targetKeys={chooseGroup !== "" ? targetKeys : []}
          onChange={handleChange}
          render={(item) => `${item.account}`}
          //footer={renderFooter}
        />
      ) : (
        <h1 style={{ width: "90%", /*margin: "auto",*/ textAlign: "center" }}>
          
        </h1>
      )}
    </>
  );
};

export default GroupTransfer;
