import React from "react";
import { DatePicker } from "antd";
import moment from "moment";

const { RangePicker } = DatePicker;
const dateOriFormat = "YYYYMMDD";
const dateFormat = "YYYY-MM-DD";

const ControlBar = ({ startTime, setStartTime, endTime, setEndTime }) => {
  const handleOnChange = (text, dateStr) => {
    //console.log(text, dateStr, moment(startTime, dateFormat));

    // setStartTime(dateStr[0]);
    // setEndTime(dateStr[1]);
    if (text !== null) {
      setStartTime(text[0].format(dateOriFormat));
      setEndTime(text[1].format(dateOriFormat));
    }
  };

  return (
    <div
      style={{
        float: "right",
        margin: "auto auto 3em auto",
        //marginBottom: "30px",
        justifyContent: "space-between",
        marginRight: "5%",
      }}
    >
      <RangePicker
        defaultValue={[
          moment(startTime, dateOriFormat),
          moment(endTime, dateOriFormat),
        ]}
        size="large"
        format={dateFormat}
        onChange={handleOnChange}
      />
    </div>
  );
};

export default ControlBar;
