import React from "react";
import { Table } from "antd";

import { FileTextOutlined } from "@ant-design/icons";

const columns = [
  {
    title: "SN",
    dataIndex: "sn",
    //render: (text) => text + 1,
  },
  {
    title: "File Name",
    dataIndex: "filename",
  },
  {
    title: "Update Time",
    dataIndex: "updatetime",
  },
  {
    title: "Report",
    dataIndex: "HtmlPath",
    render: (text) => (
      <a href={text} target="_blank" rel="noreferrer">
        <FileTextOutlined />
      </a>
    ),
  },
];

// const rowSelection = {
//   onChange: (selectedRowKeys, selectedRows) => {
//     console.log(
//       `selectedRowKeys: ${selectedRowKeys}`,
//       "selectedRows: ",
//       selectedRows
//     );
//   },
//   getCheckboxProps: (record) => ({
//     disabled: record.name === "Disabled User",
//     // Column configuration not to be checked
//     name: record.name,
//   }),
// };

const DetailTable = ({ changeData, startTime, endTime }) => {
  const total = changeData.length === 0 ? 0 : changeData[0].detail.length;

  const dataSource =
    changeData.length === 0
      ? []
      : changeData[0].detail.map((x, idx) => {
          x.key = idx;
          return x;
        });
  //const [selectionType, setSelectionType] = useState("checkbox");

  return (
    <div>
      <Table
        scroll={{ x: "100%" }}
        style={{
          width: "90%",
          margin: "auto",
          textAlign: "center",
          borderStyle: "solid",
          borderColor: "lightgrey",
          borderWidth: "1px",
          marginBottom: "50px",
          boxShadow: "0 2px 8px rgba(0, 0, 0, 0.26)",
        }}
        // rowSelection={{
        //   type: selectionType,
        //   ...rowSelection,
        // }}
        title={() =>
          changeData.length !== 0
            ? changeData[0].detail.length === 0
              ? `No report detail between ${startTime} - ${endTime}`
              : `${changeData[0].name} Details`
            : null
        }
        bordered
        //pagination={{ pageSize: 5, position: ["bottomCenter"] }}
        pagination={{
          position: ["bottomCenter"],
          pageSize: 8,
          total: total,
          showTotal: (total) => `Total ${total} files`,
        }}
        columns={columns}
        dataSource={dataSource}
        //dataSource={changeData.length === 0 ? null : changeData[0].detail}
      />
    </div>
  );
};

export default DetailTable;
